import { saleArrs } from '../../mock/api-class-goods.js';
export default {
	name: 'goods-list',
	props: {
		// 是否有特殊处理事件 默认值： 否
		eventHandle: {
			type: String,
			default: ''
		},
		hot: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			saleArrs,
			baseUrl: 'http://39.108.65.247:8087',
			numsArr: []
		}
	},
	methods: {
		calcNum(data) {
			const obj = this.numsArr.find(good => good.id === data.id);
			if (obj) {
				return obj.num;
			}
			return 0;
		},
		goodNumControl(data, tag) {
			const obj = this.numsArr.find(good => good.id === data.id);
			switch (tag) {
				case 'reduce': {
					if (obj) {
						obj.num = Math.max(0, --obj.num);
					} else {
						this.numsArr.push({
							id: data.id,
							num: Math.max(0, --obj.num)
						});
					}
					break;
				}
				case 'plus': {
					if (obj) {
						obj.num = Math.min(200, ++obj.num);
					} else {
						this.numsArr.push({
							id: data.id,
							num: Math.min(200, ++obj.num)
						});
					}
					break;
				}
				default: break;
			}
		},
		productPlus(data) {
			const obj = this.numsArr.find(good => good.id === data.id);
			if (obj) {
				obj.num = obj.num + 1;
			} else {
				this.numsArr.push({
					id: data.id,
					num: 1
				});
			}
		},
		skipTo() {
			uni.navigateTo({
			    url: "/pages/other/good-detail"
			});
		}
	}
}
